import React, { useMemo } from "react";
import { AutoSizer, Table, Column } from "react-virtualized";
import "react-virtualized/styles.css"; // Import default styles
import "./ErrorPopup.css";
import ReusableAlert from "../Alert/ReusableAlert";

const ErrorPopup = ({ errors }) => {
  console.log(`[ErrorPopup.jsx] errors:`, errors);
  const processedErrors = useMemo(() => {
    return errors.map((error) => {
      const rowMatch = error.match(/Row (\d+):/);
      const columnMatch = error.match(/"([^"]*)" (?:is required|value)/);

      return {
        rowNumber: rowMatch ? rowMatch[1] : "N/A",
        columnName: columnMatch
          ? columnMatch[1]
          : error.includes("Collaborative Space")
          ? "Collaborative Space"
          : "N/A",
        errorDescription: rowMatch ? error.split(": ")[1] : error,
      };
    });
  }, [errors]);

  return (
    <div className="error-popup-container">
      <ReusableAlert
        variant="danger"
        message={
          <>
            <strong>⚠️ Note:</strong> Below reports show only uploaded
            spreadsheet error rows. To resolve these issues, update the
            spreadsheet and re-import. ({processedErrors.length} errors found)
          </>
        }
        className="custom-alert"
      />

      <div className="table-container">
        <AutoSizer>
          {({ height, width }) => (
            <Table
              width={width}
              height={height}
              headerHeight={40}
              rowHeight={50}
              rowCount={processedErrors.length}
              rowGetter={({ index }) => processedErrors[index]}
              headerClassName="table-header"
              rowClassName="table-row"
            >
              <Column
                label="Row Number"
                dataKey="rowNumber"
                width={width * 0.15}
                className="table-cell"
                headerClassName="table-header-cell"
                cellRenderer={({ cellData }) => (
                  <div className="cell-content">{cellData}</div>
                )}
              />
              <Column
                label="Column Name"
                dataKey="columnName"
                width={width * 0.25}
                className="table-cell"
                headerClassName="table-header-cell"
                cellRenderer={({ cellData }) => (
                  <div className="cell-content">{cellData}</div>
                )}
              />
              <Column
                label="Error Description"
                dataKey="errorDescription"
                width={width * 0.6}
                className="table-cell"
                headerClassName="table-header-cell"
                cellRenderer={({ cellData }) => (
                  <div className="cell-content">{cellData}</div>
                )}
              />
            </Table>
          )}
        </AutoSizer>
      </div>
    </div>
  );
};

export default ErrorPopup;
